    cartCount (id) {
      // console.log('cartCount(id) ==>', id)
      let count = 0
      for (let i = 0; i < this.cart.length; i++) {
        if (this.cart[i] === id) {
          count++
        }
      }
      return count
    },
    sortedProducts (c) {
      // console.log('sortedProducts(c) ==>', c)
      // https://codepen.io/parths267/pen/bXbWVv
      if (c === 'max_min' || c === 'min_max' || c === 'rating' || c === 'title' || c === 'id') {
        // eslint-disable-next-line
        function compare (a, b) {
          if (c === 'max_min') {
            if (a.price < b.price) return 1
            if (a.price > b.price) return -1
            return 0
          } else if (c === 'min_max') {
            // console.log("el =>", c);
            // console.log("a.price =>", a.price);
            if (a.price < b.price) return -1
            if (a.price > b.price) return 1
            return 0
          } else if (c === 'rating') {
            if (a.rating < b.rating) return 1
            if (a.rating > b.rating) return -1
            return 0
          } else if (c === 'title') {
            if (a.title.toLowerCase() < b.title.toLowerCase()) return -1
            if (a.title.toLowerCase() > b.title.toLowerCase()) return 1
            return 0
          } else if (c === 'id') {
            if (a.id < b.id) return -1
            if (a.id > b.id) return 1
            return 0
          }
        }
        this.products = this.products.sort(compare)
      }

      return this.paginate(this.products)
      // }
    },
