            if (this.selectBrand.length === 0) {
              this.products = this.array
            } else {
              this.products = this.resCountBrand.concat(this.paradyz, this.tubadzin, this.stargres, this.cerrad)
            }
          }
        }
      })

      console.log(this.products)

      if (this.selectBrand.length === 0) this.clearFilter = false

      this.setPages()
    },
    changeRangeSlider (a, b) {
      // console.log('changeRangeSlider()')

      if (this.clearFilter) {
        this.clearFilter = true
      } else if (this.data_from === this.minPrice || this.data_to === this.maxPrice) {
        this.clearFilter = false
      }

      $('#clear-filter').fadeIn('slow')
      this.filterProduct = []
      const sortArray = this.array.map(num => {
        if (num.price >= a && num.price <= b) return num
      })
      for (let i = 0; i < sortArray.length; i++) {
        if (sortArray[i]) {
          this.filterProduct.push(sortArray[i])
        }
      }
      this.products = this.filterProduct
      this.setPages()
    },
    clearFilters () {
      // console.log('clearFilters()')
      this.clearFilter = false
      $('#clear-filter').fadeOut('slow')
      this.filterProduct = []
      this.products = this.array
      // this.rangeFilterProduct = true;
      // this.rangeFilterProducts = false;
      const resetSlider = $('.js-range-slider').data('ionRangeSlider')
      resetSlider.reset()

      this.selectCountry = []
      this.selectBrand = []
      this.ua = []
      this.ru = []
      this.by = []
      this.pl = []
      this.paradyz = []
      this.tubadzin = []
      this.stargres = []
      this.cerrad = []

      this.setPages()
    },
    rangeSlider () {
      // console.log('rangeSlider()')
      const filteredArray = this.products.map(function (val) {
        return val.price
      })
      const minValue = Function.prototype.apply.bind(Math.min, null)
      this.minPrice = minValue(filteredArray)
      const maxValue = Function.prototype.apply.bind(Math.max, null)
      this.maxPrice = maxValue(filteredArray)

      $('.js-range-slider').ionRangeSlider({
        type: 'double',
        min: this.minPrice,
        max: this.maxPrice,
        from: this.minPrice,
        to: this.maxPrice,
        grid: true,
        step: 50,
        skin: 'big',
        onFinish: (data) => {
          this.data_from = data.from
          this.data_to = data.to
          this.changeRangeSlider(data.from, data.to)
        }
      })
    }
  },
  computed: {
    // cartItemCount: function () {
    //   // return this.$store.state.store_carts.length || '' // count
    //   return this.$store.getters.cartItemCount // count
    // },
    // dark () {
    //   return this.$store.state.dark // true/false
    // },
    // showProduct () {
    //   return this.$store.state.store_showProduct // true/false
    // },
    // showAllProduct () {
    //   return this.$store.state.store_showAllProduct // true/false
    // },
    ...mapState([
      'dark',
      'store_showAllProduct',
      'store_showProduct'
    ]),
    ...mapGetters([
      'cartItemCount'
    ])
    // showCartProduct () {
    //   return this.$store.state.store_showProduct
    // }
    // buy_product () {
    //   console.log('this.$store.state.store_buy_product', this.$store.state.store_buy_product)
    //   return this.$store.state.store_buy_product
    // }
    // sortedProducts: function(){
    //   let search_array = this.array,
    //     searchString = this.query;
    //
    //   if (!searchString) {
    //     return search_array;
    //   }
    //
    //   searchString = searchString.trim().toLowerCase();
    //
    //   search_array = search_array.filter((item) => {
    //     if (item.title.toLowerCase().indexOf(searchString) !== -1) {
    //       return item;
    //     }
    //   });
    //
    //   // Return an array with the filtered data.
    //   return search_array;
    // }
  },
  created () {
    // console.log('created()')
  },
  mounted () {
    // console.log('mounted()')
    // const style = document.createElement('link')
    // style.type = 'text/css'
    // style.rel = 'stylesheet'
    // style.href = '/style/home.css'
    // document.head.appendChild(style)

    if (getCookie('dark-theme') === 'on') {
      this.$store.commit('darks')
      $('#dark-theme-button').attr('checked', 'checked')
    }
    function getCookie (name) {
      const matches = document.cookie.match(new RegExp(
        // eslint-disable-next-line
          '(?:^|; )' + name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, '\\$1') + '=([^;]*)'
      ))
      return matches ? decodeURIComponent(matches[1]) : undefined
    }
    this.buy_product = this.$store.state.store_buy_product
    this.products = this.array

    $('#previous').attr('disabled', true)
    for (let i = 2; i <= 12; i++) {
      if (i % 3 === 0) {
        this.counts.push(i)
      }
    }
    this.rangeSlider()
    this.slider()
    this.scroll()
    this.loadCountry()
    this.loadBrand()
    this.setPages()
  },
  beforeUpdate () {

    // $('.item:first').addClass('active');
  }
}

</script>

